  max-width: 500px;
  color: rgba(248, 244, 244, 0.911);
  text-shadow: 0 0 10px #000;
  font-size: 50pt;
  line-height: 60pt;
  text-align: center;
  border-bottom: 3px solid rgba(255, 255, 255, 0.342);
}
.rules:active {
  text-shadow: 0 0 1px #000;
}
.rule {
  font-family: "Arial, Helvetica, sans-serif";
  margin: auto;
  font-size: 20pt;
  text-align: center;
  width: 80vw;
  max-width: 500px;
