  width: 80vw;
  max-width: 600px;
  margin: 28vh auto auto auto;
  z-index: 3;
}
.form-input {
  font-size: 30pt;
  color: rgb(214, 23, 55);
  height: 40pt;
  width: 100%;
  background-color: rgba(141, 141, 141, 0.144);
  border: none;
  border-radius: 8pt;
  padding-left: 10px;
}
.form-input:focus {
  color: white;
  border-bottom: 2px solid white;
  outline: none;
}
.error {
  font-family: Arial, Helvetica, sans-serif;
